import Themes from '../themes.json';

import { getThemeConfig } from '../config';

const { themePreviewUrl } = getThemeConfig();

const lsOutput = `
${Themes.map((theme) => theme.name.toLowerCase()).join(', ')}

You can preview all these themes <a href="${themePreviewUrl}">in the docs</a>
`;

export const theme = async (
  args: string[],
  setTheme?: (value: string) => string,
): Promise<string> => {
  if (args.length === 0) {
    return `Usage: theme [arg]
Args:
  - ls: list all themes
  - set: set a theme
  - random: set a random theme

Example: 
  theme ls # to list all themes
  theme set Gruvbox # to set a theme`;
  }

  switch (args[0]) {
    case 'ls':
      return `${lsOutput}`;
    case 'set':
      const selectedTheme = args[1];
      return setTheme(selectedTheme);
    case 'random':
      const randomTheme = Themes[Math.floor(Math.random() * Themes.length)];
      return setTheme(randomTheme.name.toLowerCase());
  }
};
