import React from 'react';
import { useShell } from '../utils/shellProvider';
import { useTheme } from '../utils/themeProvider';
import { getBorderConfig } from '../config';
import { Input } from '../components/Input';
import { History } from '../components/History';

interface IndexPageProps {
  inputRef: React.MutableRefObject<HTMLInputElement>;
}

const IndexPage: React.FC<IndexPageProps> = ({ inputRef }) => {
  const { history } = useShell();
  const { theme } = useTheme();

  const containerRef = React.useRef(null);

  React.useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, [history]);

  const borderConfig = getBorderConfig();

  return (
    <>
      <div
        className="overflow-hidden h-full rounded"
        style={{
          borderColor: theme.yellow,
          padding: borderConfig.showBorder ? 16 : 8,
          borderWidth: borderConfig.showBorder ? 2 : 0,
        }}
      >
        <div ref={containerRef} className="overflow-y-auto h-full">
          <History history={history} />

          <Input inputRef={inputRef} containerRef={containerRef} />
        </div>
      </div>
    </>
  );
};

export default IndexPage;
